<template>
    <div id="carouselmain">
      
            <div v-if="travels.length > 0">
            <carousel :loop="true" :items="1" :margin="0" :autoplay="true" :nav="false" :dots="false">
                <div v-for="travel in travels" :key="travel.mt" class="item carousel__main-item">
                    <a href="" class="carousel__main-a">
                        <img :src="travel.img1" alt="" class="carousel__main-img">
                        <div class="carousel__filter"></div> 
                    </a>
                    <div class="carousel__main-info">
                        <div class="carousel__main-price">
                            <span class="carousel__main-since">DESDE</span>
                            {{ travel.price_from }}
                        </div>
                        <br>
                        <span class="carousel__main-current">{{ travel.currency }} {{ travel.room_type }} + {{ travel.taxes }}*</span>
                    </div>
                </div>
                </carousel>
            </div>
    </div>
</template>

<script>
import carousel from 'vue-owl-carousel'
import axios from 'axios'

export default {
    components: { 
        carousel 
        },
    name:'CarouselMain',
    data() {
        return{
            travels: []
        }
    },
    mounted() {
        let carouselmain = this;
        // Make a request
        axios.get('https://www.megatravel.com.mx/developers/cafe-carousel/carrousel-main')
        .then(function (response) {
        // handle success
        carouselmain.travels = response.data;
        })
        .catch(function (error) {
        // handle error
        console.log(error);
        })
        .then(function () {
        // always executed
        });
    }
}
</script>

<style>

.carousel__main-mt{
    position: absolute;
    display: inline-block;
    width: 100%;
    font-weight: 400;
    font-size: 12px;
    top: 0;
    left: 0;
    text-align: right;
    padding: 10px 10px;
    color: #fff;
    font-family: Arimo,sans-serif;
}
.carousel__main-info{
position: absolute;
    display: inline-block;
    
    color: #fff;
    bottom: 0;
    right: 25px;
}
.carousel__main-days {
    display: inline-block;
    width: 100%;
    text-align: center;
    font-weight: 700;
    font-size: 12px;
    margin-top: 10px;
    font-family: Arimo,sans-serif;
}
.carousel__main-price {
    display: inline-block;
    width: 100%;
    text-align: center;
    font-size: 110px;
    font-family: Arimo,sans-serif;
}
.carousel__main-since {
    font-size: 22px;
    vertical-align: middle;
    font-family: Arimo,sans-serif;
}
.carousel__main-current{
    display: inline-block;
    width: 89%;
    text-align: right;
    font-size: 18px;
    margin-bottom: 10px;
    font-family: Arimo,sans-serif;
}
</style>