<template>
    <div id="sellers">
      
            <div v-if="travels.length > 0">
            <carousel :loop="true" :items="5" :margin="10" :autoplay="true" :nav="false" :dots="false" :responsive="{0:{items:2,nav:false},500:{items:2,nav:false},600:{items:3,nav:false},1000:{items:5,nav:false}}">
                <div v-for="travel in travels" :key="travel.mt" class="item carousel__sellers-item">
                    <a href="" class="carousel__sellers-a">
                        <img :src="travel.img" alt="" class="carousel__sellers-img">
                    </a>
                    <div class="carousel__sellers-brasil">

                    <small class="carousel__sellers-days--brasil">{{ travel.days }} DÍAS / {{ travel.nights }} NOCHES</small>

                        <div class="carousel__sellers-price--brasil">
                            <span class="carousel__sellers-since--brasil">DESDE</span>{{ travel.price_from }}
                        </div><br>

                    <span class="carousel__sellers-current--brasil">{{ travel.currency }} {{ travel.room_type }}+{{ travel.taxes }}*</span>

                    </div>
                </div>
                </carousel>
            </div>
    </div>
</template>

<script>
import carousel from 'vue-owl-carousel'
import axios from 'axios'

export default {
    components: { 
        carousel 
        },
    name:'BestSellers',
    data() {
        return{
            travels: []
        }
    },
    mounted() {
        let sellers = this;
        // Make a request
        axios.get('https://www.megatravel.com.mx/developers/cafe-carousel/megaofertas')
        .then(function (response) {
        // handle success
        sellers.travels = response.data;
        })
        .catch(function (error) {
        // handle error
        console.log(error);
        })
        .then(function () {
        // always executed
        });
    }
}
</script>

<style>

.carousel__sellers-info{
background-color: #fecf34;
    color: #000;
}
.carousel__sellers-brasil{
background-color: #48a036 !important;
    
}

.carousel__sellers-days--brasil {
    display: inline-block;
    width: 100%;
    text-align: center;
    font-weight: 700;
    font-size: 12px;
    margin-top: 10px;
    font-family: Arimo,sans-serif;
    color: #fff;
}
.carousel__sellers-price--brasil {
        display: inline-block;
    width: 100%;
    text-align: center;
    font-size: 60px;
    font-family: Arimo,sans-serif;
    color: #fff;
}
.carousel__sellers-since--brasil {
    font-size: 14px;
    vertical-align: middle;
    font-family: Arimo,sans-serif;
    color: #fff;
}
.carousel__sellers-current--brasil {
    display: inline-block;
    width: 89%;
    text-align: right;
    font-size: 14px;
    margin-bottom: 10px;
    font-family: Arimo,sans-serif;
    color: #fff;
}

</style>