<template>
    <div id="sellers">
      
            <div v-if="travels.length > 0">
            <carousel :loop="true" :items="5" :margin="10" :autoplay="true" :nav="false" :dots="false" :responsive="{0:{items:2,nav:false},500:{items:2,nav:false},600:{items:3,nav:false},1000:{items:5,nav:false}}">
                <div v-for="travel in travels" :key="travel.mt" class="item carousel__sellers-item">
                     <a href="" class="carrusel__megatraveler-a">
                        <img :src="travel.img" alt="" class="carrusel__megatraveler-img">
                    </a>
                </div>
                </carousel>
            </div>
    </div>
</template>

<script>
import carousel from 'vue-owl-carousel'
import axios from 'axios'

export default {
    components: { 
        carousel 
        },
    name:'Megatraveler',
    data() {
        return{
            travels: []
        }
    },
    mounted() {
        let megatraveler = this;
        // Make a request
        axios.get('https://www.megatravel.com.mx/developers/cafe-carousel/temporada')
        .then(function (response) {
        // handle success
        megatraveler.travels = response.data;
        })
        .catch(function (error) {
        // handle error
        console.log(error);
        })
        .then(function () {
        // always executed
        });
    }
}
</script>

<style>

.carousel__sellers-info{
background-color: #fecf34;
    color: #000;
}
.carousel__sellers-days {
    display: inline-block;
    width: 100%;
    text-align: center;
    font-weight: 700;
    font-size: 12px;
    margin-top: 10px;
    font-family: Arimo,sans-serif;
}
.carousel__sellers-price {
        display: inline-block;
    width: 100%;
    text-align: center;
    font-size: 60px;
    font-family: Arimo,sans-serif;
}
.carousel__sellers-since {
    font-size: 14px;
    vertical-align: middle;
    font-family: Arimo,sans-serif;
}
.carousel__sellers-current {
    display: inline-block;
    width: 89%;
    text-align: right;
    font-size: 14px;
    margin-bottom: 10px;
    font-family: Arimo,sans-serif;
}

</style>