<template>
    <div id="favorites">
      
            <div v-if="travels.length > 0">
            <carousel :loop="true" :items="5" :margin="10" :autoplay="true" :nav="false" :dots="false" :responsive="{0:{items:2,nav:false},500:{items:2,nav:false},600:{items:3,nav:false},1000:{items:5,nav:false}}">
                <div v-for="travel in travels" :key="travel.mt" class="item carousel__favorites-item">
                    <a href="" class="carousel__favorites-a">
                        <img :src="travel.img" alt="" class="carousel__favorites-img">
                    </a>
                    <div class="carousel__favorites-mt">{{ travel.mt}}</div>
                    <div class="carousel__favorites-info">
                        <small class="carousel__favorites-days">{{ travel.days }} DÍAS / {{ travel.nights }} NOCHES</small>
                        <div class="carousel__favorites-price"> 
                            <span class="carousel__favorites-since">DESDE</span>{{ travel.price_from }}
                        </div> <br>
                        <span class="carousel__favorites-current">{{ travel.currency }} {{ travel.room_type }}+{{ travel.taxes }}*</span>
                    </div>
                </div>
                </carousel>
            </div>
    </div>
</template>

<script>
import carousel from 'vue-owl-carousel'
import axios from 'axios'

export default {
    components: { 
        carousel 
        },
    name:'Favorities',
    data() {
        return{
            travels: []
        }
    },
    mounted() {
        let favorites = this;
        // Make a request
        axios.get('https://www.megatravel.com.mx/developers/cafe-carousel/favoritos')
        .then(function (response) {
        // handle success
        favorites.travels = response.data;
        })
        .catch(function (error) {
        // handle error
        console.log(error);
        })
        .then(function () {
        // always executed
        });
    }
}
</script>

<style>

.carousel__favorites-mt{
    position: absolute;
    display: inline-block;
    width: 100%;
    font-weight: 400;
    font-size: 12px;
    top: 0;
    left: 0;
    text-align: right;
    padding: 10px 10px;
    color: #fff;
    font-family: Arimo,sans-serif;
}
.carousel__favorites-info{
position: absolute;
    display: inline-block;
    width: 100%;
    color: #fff;
    bottom: 0;
    left: 0;
}
.carousel__favorites-days {
    display: inline-block;
    width: 100%;
    text-align: center;
    font-weight: 700;
    font-size: 12px;
    margin-top: 10px;
    font-family: Arimo,sans-serif;
}
.carousel__favorites-price {
    display: inline-block;
    width: 100%;
    text-align: center;
    font-size: 60px;
    font-family: Arimo,sans-serif;
}
.carousel__favorites-since {
    font-size: 14px;
    vertical-align: middle;
    font-family: Arimo,sans-serif;
}
.carousel__favorites-current{
display: inline-block;
    width: 89%;
    text-align: right;
    font-size: 14px;
    margin-bottom: 10px;
    font-family: Arimo,sans-serif;
}
</style>